import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import Axios from 'axios'
import Spinner from 'react-bootstrap/spinner'
class Medicines extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            selected : false,
            item : [],
            loading:true
        }
    }
    getMedicine = (event)=>{
        this.setState({selected:true,item:event.target.innerHTML});
    }
    fetchList = ()=>{
        Axios.get('https://medicateindia.herokuapp.com/products')
        .then(res =>{
            this.setState({item:res.data,loading:false})
        })
    }
    componentDidMount()
    {
        this.fetchList();
    }
    render() {
        let character = 'A';
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
        return (<React.Fragment>
            <div className="container mt-2">
                <h3>Find Your Prescription Medicines</h3>
                <h6><i>Shop by Drug Category</i></h6>
                <div className="container d-flex flex-wrap align-self-start">
                        {
                            this.state.item.map((item,index)=>{
                                if(item.type.toString().charAt(0) !== character)
                                {
                                    character = item.type.toString().charAt(0)
                                    return (<React.Fragment><div style={{flexBasis:"100%",height:"0"}}/>
                                        <h3 className="text-capitalize my-2 mr-2">{character}</h3>
                                        <li key={index} className="list-group-item text-capitalize"><Link to={"medicines/"+item.type} className="text-decoration-none">{item.type}</Link></li>
                                    </React.Fragment>
                                )}
                                else
                                return (
                                <li key={index} className="list-group-item text-capitalize "><Link to={"medicines/"+item.type} className="text-decoration-none">{item.type}</Link></li>
                                )
                            })
                        }
                </div>
            </div>
    </React.Fragment>
        )
    }
}

export default Medicines
