import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class LogIn extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            logedin : false,
            email : '',
            pass : '',
            loading:false
        }
    }
    componentDidMount()
    {
        if(localStorage.token)
        {
            this.props.history.push('/account');
        }
    }
    submitHandle = (event)=>{
        event.preventDefault();
        this.setState({email : event.target.email.value,pass:event.target.pass.value,loading:true})
        setTimeout(()=>axios.post('https://medicateindia.herokuapp.com/api/user/login',{
                        email : this.state.email,
                        pass : this.state.pass
                    }).then((res)=>{
                        this.setState({loading:false})
                        if(!res.data.token)
                        {
                            this.setState({showAlert:true,alertMessage :res.data})
                        }
                        else{
                            localStorage.setItem('token',res.data.token);
                            this.props.history.push(`/`)
                        }
                    })
                    .catch((err)=>{
                        console.log(err);
                    })
                )
    }
    alertHandle = ()=>{
        this.setState({showAlert:!this.state.showAlert})
    }    
    render()
    {
        const alertShow = this.state.showAlert ? 'd-block' : 'd-none'
        if(this.state.loading)
        {
              return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }        
        return  <React.Fragment>
                    <div className="container mt-4">
                        <div className="row">
                            <div className="offset-md-3 offset-lg-3 offset-sm-2 col-md-6 col-lg-6 col-sm-6">
                                <h3 styled="font-family: 'Karona One'">Log In To Your Account</h3>
                            <hr/>
                            </div>
                        </div>
                        <div className='row' className={alertShow}>
                        <div className="offset-md-4 offset-lg-4 offset-sm-1 col-md-4 col-lg-4 col-sm-4">
                            <div className="alert alert-danger alert-dismissible">
                                  <button type="button" className="close" onClick={this.alertHandle}>&times;</button>
                                <strong> {this.state.alertMessage}</strong>
                              </div>
                          </div>
                      </div>
                        <form onSubmit={this.submitHandle}>
                            <div className="row form-group">
                                <div className="offset-md-3 offset-lg-3 offset-sm-2 col-md-6 col-lg-6 col-sm-5">
                                    <label htmlFor="email">Email :</label>
                                    <input required className="form-control" autoFocus type="text" id="email"/>
                                </div>
                            </div>
                            <div className="row form-group">
                                <div className="offset-md-3 offset-sm-2 offset-lg-3 col-md-6 col-lg-6 col-sm-5">
                                    <label htmlFor="pass">Password :</label>
                                    <input required className="form-control" type="password" id="pass"/>
                                </div>
                            </div>
                            <div className="row form-group">
                                <div className="offset-md-3 offset-sm-1 offset-lg-3 col-md-2 col-lg-2 col-sm-1">
                                    <input type="submit" value="Sign In" id="sub-btn" className="btn btn-lg submit text-white"/>
                                </div>
                                <div className="offset-md-2 offset-sm-1 offset-lg-2 col-md-2 col-lg-2 col-sm-1">
                                    <Link to="signup" title="Create New Account">Don't have account?</Link>
                                </div>
                            </div>
                        </form>
                    </div>
                </React.Fragment>    
    }
}

export default LogIn