import React, { Component } from 'react'
import axios from 'axios';
import {Link} from 'react-router-dom'
import PageNotFound from './PageNotFound';
import Spinner from 'react-bootstrap/spinner'
class FitnessBrands extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            items : [],
            loading : true
        }
    }
    fetchProducts = ()=>{
        // d7hjdvye4sky5cdwmmmtf3bf
        // bxktfmp48ryvgntbpwx3ybcz
        axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/paginated/items?format=json&category=976760_1166769&brand=${this.props.match.params.brandName}&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
        .then(res=>{
            console.log(res);
            if(res.data.items.length === 0)
            {
                this.setState({err:true})
            }
            else this.setState({items : res.data.items,loading:false});
        })
        .catch(err=>{
            alert(err);
        });
    }
    componentDidMount()
    {
        this.fetchProducts();
    }
    render() {
        if(this.state.err)
        {
            return <PageNotFound/>
        }
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
        return (
            <div>
                <div className="container-fluid mt-3 ">
                    <div className="d-flex justify-content-between">
                        <h2 className="text-capitalize">{this.props.match.params.brandName}</h2>
                    </div>
                    <hr/>
                    <div className="container mt-2 mx-auto">
                        <div className="row">
                            {
                                this.state.items.map((item,index)=>{
                                    return (
                                    <div className="item card col-10 col-sm-5 col-md-3 col-lg-2 col-xs-10 mb-1" key={index}>
                                        <Link style={{textDecoration:"none"}} className="text-dark" to={"/item/"+item.itemId}>
                                        <img className="card-img-top m-2" src={item.mediumImage} style={{height:"8rem"}} alt="product image"/>
                                        <div className="card-body">
                                        <h6 title={item.name} style={{height:"5rem"}} className="overflow-hidden card-title">{item.name}</h6>
                                        <p className="card-text"><b><i className="fas fa-rupee-sign"/> {(Number(item.salePrice)*71.29).toFixed(2)}</b></p>
                                        {/* <button className="btn btn-info btn-b">ADD TO CART</button> */}
                                        </div>
                                        </Link>
                                    </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default FitnessBrands