import React, { Component } from 'react'
import axios from 'axios';
import {Link} from 'react-router-dom'
import PageNotFound from './PageNotFound';
import $ from 'jquery'
import Spinner from 'react-bootstrap/spinner'
class FitnessCategories extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            items : [],
            loading : true,
            start : 1,
            allFetched : false,
            LoadingMore : false,
            sort : 'relevance'
        }
    }
    fetchProducts = ()=>{
        // d7hjdvye4sky5cdwmmmtf3bf
        // bxktfmp48ryvgntbpwx3ybcz
        axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.props.match.params.item}&categoryId=976760_1166769&start=${this.state.start}&sort=${this.state.sort}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
        .then(res=>{
            console.log(res);
            if(res.data.items.length === 0)
            {
                this.setState({err:true})
            }
            else this.setState({items : res.data.items,loading:false});
        })
        .catch(err=>{
            alert(err);
        });
    }
    fetchMore = ()=>{
        axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.props.match.params.item}&categoryId=976760_1166769&start=${this.state.start}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
        .then(res=>{
            if(res.data.message === "Results not found!")
            {
                this.setState({allFetched:true,LoadingMore:false})
            }
            else{
                let items = this.state.items;
                items = items.concat(res.data.items)
                this.setState({items:items,LoadingMore:false})
            }
        })
        .catch(err=>{
            alert(err)
            this.setState({err:true})
        });        
    }
    componentDidMount()
    {
        this.fetchProducts();
    }
    componentDidUpdate()
    {
            $(window).scroll(()=> 
            {
                if(($(window).scrollTop() === ($(document).height() - $(window).height())) && (!this.state.LoadingMore && !this.state.allFetched)) 
                {
                    this.setState({LoadingMore:true})
                    let start = this.state.start;
                    this.setState({start:start+12});
                    this.fetchMore()
                }
            })
    }
    selectHandle = (event)=>{
        console.log(event.target.value)
        this.setState({sort:event.target.value,start:1,allFetched : false,LoadingMore : false,})
        setTimeout(()=>this.fetchProducts());
    }
    render() {
        const showLoad = this.state.LoadingMore ? "d-flex justify-content-center" : "d-none"
        const showEnd = this.state.allFetched ? "d-flex justify-content-center" : "d-none"
        if(this.state.err)
        {
            return <PageNotFound/>
        }
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
        return (
            <div>
                <div className="container-fluid mt-3 ">
                    <div className="d-flex justify-content-center">
                        <h1 className="text-capitalize"><b>{this.props.match.params.item}</b></h1>
                        {/*<form className="form-group">
                            <select defaultValue="relevance" mutiple className="form-control" onChange={(e)=>this.selectHandle(e)}>
                                <option value="relevance">Relevance</option>
                                <option value="price&order=asc">Price low to high</option>
                                <option value="price&order=desc">Price high to low</option>
                            </select>
                        </form>*/}
                    </div>
                    <hr/>
                    <div className="container mt-2 mx-auto">
                        <div className="row">
                            {
                                this.state.items.map((item,index)=>{
                                    return (
                                    <div className="item card col-10 col-sm-5 col-md-3 col-lg-2 col-xs-10 mb-1" key={index}>
                                        <Link style={{textDecoration:"none"}} className="text-dark" to={"/item/"+item.itemId}>
                                        <img className="card-img-top m-2" src={item.mediumImage} style={{height:"8rem"}} alt=""/>
                                        <div className="card-body">
                                        <h6 title={item.name} style={{height:"5rem"}} className="overflow-hidden card-title">{item.name}</h6>
                                        <p className="card-text"><b><i className="fas fa-rupee-sign"/> {(Number(item.salePrice)*71.29).toFixed(2)}</b></p>
                                        {/* <button className="btn btn-info btn-b">ADD TO CART</button> */}
                                        </div>
                                        </Link>
                                    </div>
                                    )
                                })
                            }
                        </div>
                        <div className={"mt-4 mb-4 "+showLoad}>
                            <Spinner animation="border" role="status" variant="primary">
                              <span className="sr-only">Loading...</span>
                            </Spinner>
                        </div>
                        <div className={"mt-4 mb-4 "+showEnd}>
                             <h3><i className="fas fa-heart-broken"/> OOF! You've reached the end!</h3>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default FitnessCategories