import React, { Component } from 'react'
import {Link} from 'react-router-dom'
class PersonalCare extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            start : 1,
            products : ["body care","eye care","face care","hair care","lip care","skin care"],
            brands : [{img:"../personalCare/adidas.jpg",link:"Adidas"},
                      {img:"../personalCare/almay.jpg",link:"Almay"},
                      {img:"../personalCare/avalonOrganics.jpg",link:"Aveeno"},
                      {img:"../personalCare/bioderma.jpg",link:"Bioderma"},
                      {img:"../personalCare/cleanAndClear.png",link:"Clean & Clear"},
                      {img:"../personalCare/chapstick.png",link:"ChapStick"},
                      {img:"../personalCare/essie.png",link:"Essie"},
                      {img:"../personalCare/garnier.jpg",link:"Garnier"},
                      {img:"../personalCare/loreal.jpg",link:"Loreal Paris"},
                      {img:"../personalCare/maybelline.jpg",link:"Maybelline"},
                      {img:"../personalCare/neutrogena.jpg",link:"Neutrogena"},
                      {img:"../personalCare/olay.png",link:"Olay"},
                      {img:"../personalCare/ponds.jpg",link:"Ponds"},
                      {img:"../personalCare/salonPerfect.jpg",link:"Salon Perfect"},
                      {img:"../personalCare/vanicream.jpg",link:"Vanicream"}]

        }
    }
    beautyByBrands = ()=>{
    }
    componentDidMount()
    {
        this.beautyByBrands();
    }
    render() {
        return (
            <div className="container">
                <div className="d-flex justify-content-between container mt-3">
                    <ul className="list-group" type="none">
                        <h4>Categories</h4>{
                            this.state.products.map((item,index)=>{
                                return <Link to={"/personalcare/"+item}  className="text-capitalize list-group-item list-group-item-action" key={index}>{item}</Link>
                            })
                        }
                    </ul>
                    <img src="../personalCare.png" className="d-sm-inline d-md-inline d-lg-inline d-none" width="85%" height="50%"/>
                </div>
                <hr/>
                <div className="text-center">
                    <h2 style={{fontFamily:"Libre Baskerville"}}><b>Shop By Brands</b></h2>
                    <div className="row mb-5">
                    {
                        this.state.brands.map((item,qty)=>{
                            return <Link class="card mx-auto mb-1" to={'/personalcare/shopByBrand/'+item.link}> 
                                        <div className="my-auto"  style={{width: "14rem"}}>
                                            <img class="card-img-top" src={item.img} alt="Card image cap"/>
                                        </div>
                                    </Link>
                        })
                    }
                    </div>
                </div>
            </div>
        )
    }
}

export default PersonalCare
