import React,{Component} from 'react'
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'

class OrderHistory extends Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			loading : true,
			products : []
		}
	}
	fetchItemsFromApi = async (item,paymentMode,address,a)=>{
		//console.log(item,paymentMode,a)
		let id = item.id
        await axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/items?ids=${id}&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
        .then(docs=>{
        	let product = this.state.products;
        	//console.log(products[a])
        	if(!product[a])
        		product[a] = new Array()
        	product[a].push({details : [{item:docs}],paymentMode:paymentMode,address:address,qty:item.qty})
        	this.setState({products:product,loading:false})
        })
        .catch(err=>{
            alert(err);
        })
        
    }
	componentDidMount()
	{
		axios.get('https://medicateindia.herokuapp.com/api/userData/orderHistory',{
			headers :{
				'authorization':"JWT "+localStorage.token
			}
		})
		.then(async res=>{
			if(res.data.orderHistory.length)
			{
				for(var i=0;i<res.data.orderHistory.length;i++)
				{
					for(var j = 0;j<res.data.orderHistory[i].products.length;j++)
					{
						await this.fetchItemsFromApi(res.data.orderHistory[i].products[j],res.data.orderHistory[i].paymentMode,res.data.orderHistory[i].address,i)
					}
				}
			}
			else{
				this.setState({noOrders : true})
			}
			//this.setState({loading:false})
		})
		.catch(err=>{
			if(err.response && err.response.status === 403)
			{
				alert('Session Expired');
				this.props.history.push('/login');
			}
		})
	}
	render()
	{
		if(this.state.noOrders)
		{
			return <div className="container d-flex justify-content-center mt-2">
				<h1 className="text-muted">No Orders</h1>
			</div>
		}
		if(this.state.loading)
		{
			return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
		}
		return (
			<React.Fragment>
				<div className="container mb-5">
				{
					this.state.products.map((details,index)=>{
						//console.log(details)
						return (
							<div className="card mt-2">
								{
									details.map((item,key)=>{
										return (
											<div className="card-body mt-1">{
												item.details.map((a,i)=>{
													return <div className="d-flex">
															<img alt="" src={a.item.data.items[0].largeImage} style={{height:"8rem"}}/>
															<div>
																<h5>{a.item.data.items[0].name}</h5>
															</div>
														</div>
												})
											}
											
												<div><b>Qty :</b> {item.qty}</div>
											</div>
										)
									})
								}
								<div className="card-footer">
								<div className="d-flex justify-content-between">
									<div>
										<b>Payment Mode :</b> {details[0].paymentMode}
									</div>
									<div><b>Shipping Address :</b> {details[0].address}</div>
								</div>
								</div>

							</div>
							)
					})
				}
				</div>
			</React.Fragment>
			)
	}
}

export default OrderHistory