import React , {Component} from 'react';
import {Link} from 'react-router-dom'
import AdminDetails from './AdminDetails'
import Users from './Users'
import Products from './Products'
class AdminPanel extends Component {
	constructor(props)
    {
        super(props);
        this.state = {
            menu:false,
            mode:1
        }
        this.toggler = this.toggler.bind(this);
    }
    logout = () =>{
        sessionStorage.clear();
        this.props.history.push('/admin')
    }
    componentDidMount()
    {
        document.getElementById('navbar').style.display="none"
        document.getElementById('footer').style.display="none"
        if(!sessionStorage.token)
        {
            sessionStorage.clear()
            this.props.history.push('/admin');
        }
    }
    toggler()
    {
        this.setState({menu : !this.state.menu});
    }
    openSide = ()=>{
    	document.getElementById("mySidepanel").style.width = "180px";
    }
    closeSide = ()=>{
    	document.getElementById("mySidepanel").style.width = "0";
    }
    getUser = ()=>{
    	this.setState({mode:2})
    }
    getDetails = ()=>{
    	this.setState({mode:1})
    }
    getComponent = (a)=>{
    	const comp = {
    		1 : <AdminDetails/>,
            2:	<Users/>,
            3: <Products/>
    	}
    	return comp[a]
    }
    getProducts = ()=>{
        this.setState({mode:3})
    }
    render() {
    	return(
			<React.Fragment>
                <nav className=" navbar navbar-expand-md navbar-dark Homepage-navbar">
                <button onClick={this.openSide} className="bg-transparent border-0 text-light" style={{fontSize:"25px",outline:0}}>☰</button>
                    <Link className="navbar-brand ml-3" to="/adminpanel">
                        <div className="logo">
                            <h4 className="text-center text-light" ><i className="fas fa-heartbeat"></i> Medicate</h4>
                            <p style={{fontSize:"10px"}} className="text-center text-light" >Health aapki, fikar hamari</p>
                        </div>
                    </Link>
	                    <ul className="navbar-nav ml-3 ml-md-auto ml-lg-auto ml-sm-auto mr-3">
	                        <li className="nav-item my-3">
	                            <button className="nav-link bg-transparent border-0" style={{outline:0}} onClick={this.logout}><div className="d-none d-sm-inline d-md-inline d-lg-inline">Log Out &nbsp;</div><i className="fas fa-sign-out-alt"></i> </button>
	                        </li>
	                    </ul>
          		</nav>
          		<div>
          			{this.getComponent(this.state.mode)}
          		</div>
				<div id="mySidepanel" className="sidepanel">
  					<button className="closebtn bg-transparent border-0" onClick={this.closeSide}>×</button>
  					<button className="bg-transparent border-0" onClick={this.getDetails}>Home</button>
  					<button className="bg-transparent border-0" onClick={this.getUser}>Users</button>
				  	<button className="bg-transparent border-0" onClick={this.getProducts}>Medicines</button>
				  	
				</div>
          			
            </React.Fragment>
			)
	}
}

export default AdminPanel