import React, { Component } from 'react'
import axios from 'axios';
import PageNotFound from './PageNotFound';
import Spinner from 'react-bootstrap/Spinner'
import Modal from 'react-bootstrap/Modal'
class ShowItem extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            item : [],
            loading : true,
            err : false,
            converterPrice:0,
            token : localStorage.token,
            showModal : false
        }
    }
    componentDidMount()
    {
        axios.get('https://api.exchangerate-api.com/v4/latest/USD')
            .then(res=>{
                this.setState({converterPrice:res.data.rates.INR})
            })
        // d7hjdvye4sky5cdwmmmtf3bf
        // bxktfmp48ryvgntbpwx3ybcz
        setTimeout(()=>
        {
            axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/items?ids=${this.props.match.params.id}&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
            .then(res=>{
                if(res.data.items.length === 0)
                {
                    this.setState({err:true})
                }
                else this.setState({item : res.data.items[0],loading:false});
            })
            .catch(err=>{
                this.setState({err :true})
            });
        })
    }
    addToCartHandle = (event)=>
    {
        if(this.state.token)
        {
            const qty = document.getElementById('sel').value
            //event.target.clas
            //console.log(event.target.style.display='none')
            axios.post('https://medicateindia.herokuapp.com/api/userData/addToCart',{
                id:this.props.match.params.id,
                qty : qty
            },{
                    headers: {
                    'authorization': "JWT "+this.state.token
                }
            })
            .then(res=>{
                this.setState({showModal:true,ModalMessage:res.data})
            })
            .catch(err=>{
                console.log(err)
            })
        }
        else{
            alert('Login First')
        }
    }
    modalHandle = (val)=>{
        this.setState({showModal:val})
    }
    render() {
        if(this.state.err)
        {
            return <PageNotFound/>
        }
        if(this.state.loading)
        {
            return (<div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
            )
        }
        return (
            <React.Fragment>
                <Modal size="sm" show={this.state.showModal} onHide={()=>this.modalHandle(false)}>
                    <Modal.Header closeButton>
                    <Modal.Title><i className="fas fa-exclamation-triangle"></i></Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <h5>{this.state.ModalMessage}</h5>
                    </Modal.Body>
                </Modal>
                <div className="container mt-3 mb-3 d-flex">
                    <div className="card d-flex" style={{width:"50em"}}>
                        <div className="m-4">
                            <div className="d-flex">
                                <img alt="images" src={this.state.item.largeImage} style={{height:"10rem"}}></img>
                                <div>
                                    <h4>{this.state.item.name}</h4>
                                    <h6><b><span className="text-danger">Rs.</span> {(Number(this.state.item.salePrice)*this.state.converterPrice).toFixed(2)}</b></h6>
                                </div>
                            </div>
                            <div className="d-flex justify-content-end">
                                <div className="border rounded d-flex">
                                    <label htmlFor="sel" className="pt-2 pl-2">QTY&nbsp;:</label>
                                    <select style={{outlineColor:"inherit"}} name="Qty" className="border-0 form-control" id="sel">
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                    </select>
                                </div>
                                <button className="ml-2 btn btn-info" onClick={(event)=>this.addToCartHandle(event)}><b>ADD TO CART</b></button>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default ShowItem
