import React, { Component } from 'react'
import {Link} from 'react-router-dom'
class PersonalCare extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            start : 1,
            products : ["protein supplements","high protein foods","isopure protein drinks","mass gainers","muscle building supplements","natural products for fitness","optimum nutrition"],
            brands : [{img:'../fitness/ON.jpg',link:'Optimum'},
                    {img:'../fitness/orgain.jpg',link:'Orgain'},
                    {img:'../fitness/protein2o.png',link:'Protein2o'},
                    {img:'../fitness/quest.jpg',link:'Quest'},
                    {img:'../fitness/SixStarPro.jpg',link:'Six Star Pro'},
                    {img:'../fitness/specialK.png',link:'Special K'},
                    {img:'../fitness/Sunwarrior.jpg',link:'Sunwarrior'},
                    {img:'../fitness/thinkThin.jpg',link:'thinkThin'},
                    {img:'../fitness/vega.jpg',link:'Vega'},
                    {img:'../fitness/zonePerfect.png',link:'ZonePerfect'}
            ]
        }
    }
    beautyByBrands = ()=>{
    }
    componentDidMount()
    {
        this.beautyByBrands();
    }
    render() {
        return (
            <div className="container">
                <div className="d-flex justify-content-between container mt-3">
                    <ul className="list-group" type="none">
                        <h4>Categories</h4>{
                            this.state.products.map((item,index)=>{
                                return <Link to={"/fitness/categories/"+item} className="text-capitalize list-group-item list-group-item-action" key={index}>{item}</Link>
                            })
                        }
                    </ul>
                    <img src="../fitness.png" className="d-sm-inline d-md-inline d-lg-inline d-none" width="70%" height="50%"/>
                </div>
                <hr/>
                <div className="text-center">
                    <h2 style={{fontFamily:"Libre Baskerville"}}><b>Shop By Brands</b></h2>
                    <div className="row">
                    {
                        this.state.brands.map((item,qty)=>{
                            return <Link className="card mx-auto mb-1" to={'/fitness/shopByBrand/'+item.link}> 
                                        <div className="my-auto mx-auto"  style={{width: "14rem"}}>
                                            <img className="card-img-top" src={item.img} alt=""/>
                                        </div>
                                    </Link>
                        })
                    }
                    </div>

                </div>
            </div>
        )
    }
}

export default PersonalCare
