import React,{Component} from 'react'
import {Link} from 'react-router-dom';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class Cart extends Component
{
	constructor(props)
    {
        super(props)
        this.state = {
            token : localStorage.token,
            redirectToLogin : false,
            loading:true,
            items : [],
            total : 0,
            converterPrice : 0,
            cart : []
        }
    }
    fetchItemsFromApi = (items)=>{
        items = items.toString();
        axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/items?ids=${items}&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
        .then(docs=>{
            this.setState({items:docs.data.items,loading:false})
        })
        .catch(err=>{
            alert(err);
        })
        
    }
	fetchCartData = ()=>{
        axios.get('https://medicateindia.herokuapp.com/api/userData/cartData',{
            headers: {
                'authorization': "JWT "+this.state.token
            }
        })
        .then(res=>{
        	if(res.data.cart.length === 0)
            {
                this.setState({empty : true,loading:false})
            }
            else{
                this.setState({cart:res.data.cart})
                let arr = []
                console.log(res.data.cart)
                for(var i=0;i<res.data.cart.length;i++)
                {
                    arr.push(res.data.cart[i].id);
                }
                this.fetchItemsFromApi(arr)
            }
        })
        .catch(err=>{
            alert(err)
        })
    }
    componentDidMount()
    {
        if(!this.state.token)
        {
            alert('You are not Loged In')
            this.props.history.push('/login')
        }
        else{
            axios.get('https://api.exchangerate-api.com/v4/latest/USD')
            .then(res=>{
                this.setState({converterPrice:res.data.rates.INR})
            })
            setTimeout(()=>this.fetchCartData());
        }
    }
    deleteHandle=(a)=>{
        this.setState({loading:true})
        axios.post('https://medicateindia.herokuapp.com/api/userData/deleteFromCart',{
            id:a
        },{
            headers: {
                'authorization': "JWT "+this.state.token
            }
        })
        .then(res=>{
            if(res.data === 'Deleted')
            {
                
                this.fetchCartData()
            }
        })
    }
    selectHandle = (a,event)=>{
        axios.post('https://medicateindia.herokuapp.com/api/userData/increaseQty',{
            id:a,
            qty:event.target.value
        },{
            headers : {
                'authorization': "JWT "+this.state.token   
            }
        }).then(res=>{
            this.setState({loading:true})
            this.fetchCartData()            
        })
    }
	render()
	{
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                        <Spinner animation="border" role="status" variant="primary">
                          <span className="sr-only">Loading...</span>
                        </Spinner>
                    </div>
        }
        if(this.state.empty)
        {
            return <div className="d-flex justify-content-center display-4 text-muted mt-4"><i className="fas fa-shopping-cart"/> Your Shopping Cart is Empty</div>
        }
        var rupees = 0
        return <React.Fragment>
                <div className="container">
                <h1 className="my-2">Your Cart</h1>
                    <div className="mt-1 row">
                    {
                        this.state.items.map((item,index)=>
                        {
                            rupees = rupees + item.salePrice*this.state.cart.find(a => a.id === item.itemId).qty
                            return  <div className="col-10 offset-1">
                                        <div className="mt-1 d-flex">
                                            <div className="card d-flex" style={{width:"100%"}}>
                                                <div className="m-4">
                                                    <div className="d-flex">
                                                        <img alt="" src={item.mediumImage} style={{height:"5rem"}}></img>
                                                        <div>
                                                            <h6>{item.name}</h6>
                                                            <h6><b><span className="text-danger">Rs.</span> {(Number(item.salePrice)*this.state.converterPrice).toFixed(2)}</b></h6>
                                                        </div>
                                                    </div>
                                                    <div className="d-flex justify-content-end">
                                                        <div className="border rounded d-flex">
                                                            <label htmlFor="sel" className="pt-2 pl-2">QTY&nbsp;:</label>
                                                            <select defaultValue={this.state.cart.find(a => a.id === item.itemId).qty} style={{outlineColor:"inherit"}} name="Qty" onChange={(event)=>this.selectHandle(item.itemId,event)} className="border-0 form-control" id="sel">
                                                                <option value="1">1</option>
                                                                <option value="2">2</option>
                                                                <option value="3">3</option>
                                                                <option value="4">4</option>
                                                                <option value="5">5</option>
                                                            </select>
                                                        </div>
                                                        <button className="ml-2 btn btn-danger" onClick={()=>this.deleteHandle(item.itemId)}><b>DELETE</b></button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                        })
                    }
                        <div className="col-10 offset-1 mt-2 d-flex justify-content-end">
                            <h3><b>Total :</b> Rs. {(Number(rupees)*this.state.converterPrice).toFixed(2)}</h3>
                        </div>
                    </div>
                    <div className="d-flex justify-content-center my-3">
                        <Link className="btn btn-lg btn-success" to={{pathname :"/buy/selectAddress", state:{price : (Number(rupees)*this.state.converterPrice).toFixed(2),items : this.state.cart}}}>PROCEED TO BUY</Link>
                    </div>
                </div>
        </React.Fragment>
	}
}
export default Cart