import React,{Component} from 'react'
import {Link} from 'react-router-dom';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
import Modal from 'react-bootstrap/Modal'
class SelectAddress extends Component{
	constructor(props)
	{
		super(props)
		this.state = {
			token:localStorage.token,
			loading : true,
			modalShow : false,
			address : []
		}
	}
	fetchAddress = ()=>{
		axios.get('https://medicateindia.herokuapp.com/api/userData/address',{
			headers: {
                'Authorization': "JWT "+this.state.token
            }
		})
		.then(res=>{
			this.setState({address : res.data.address,loading:false})
		})
		.catch(err=>{
			if(err.response.status === 403)
			{
				alert('Session Expired')
				localStorage.clear()
				this.props.history.push('/')
			}
			else{
				alert(err.response.data)
			}
		})
	}
	componentDidMount()
	{
    console.log(this.props)
    if(!this.props.location.state)
    {
      this.props.history.push('/cart')
    }
		this.fetchAddress()
	}
	addressSubmitHandle = (event)=>{
        event.preventDefault();
        if(event.target.address.value !== '')
        {
            axios.post('https://medicateindia.herokuapp.com/api/userData/addAddress',
            {
                address : event.target.address.value
            },{
               	headers: {
                	'authorization': "JWT "+this.state.token
            	}
        	})
            .then(res=>{
                this.setModalShow(false)
                this.setState({loading:true})
                this.fetchAddress()
            })
        }   
    }
	setModalShow = (a)=>{
        this.setState({modalShow : a})
    }
	render()
	{
		if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                        <Spinner animation="border" role="status" variant="primary">
                          <span className="sr-only">Loading...</span>
                        </Spinner>
                    </div>
        }
       	
       	return <React.Fragment>
       	<div className="container">
       			<div className="row">
       				{
       					this.state.address.map((address,index)=>{
		       				return <div className="col-4 my-2" key={index}>
		                        <Link className="text-decoration-none text-dark" to={{pathname : "/buy/selectPayment",state:{price : this.props.location.state.price,address:address,items : this.props.location.state.items } } }><div class="card" style={{width: "18rem"}}>
		                          <div class="card-body">
		                            <h5 class="card-title">{address}</h5>
		                          </div>
		                        </div></Link>
		                    </div>
		                })
	                }
       			</div>
       			<button className="btn btn-success btn-lg" onClick={()=>this.setModalShow(true)}>+ Add New Address</button>
                <Modal show={this.state.modalShow}  onHide={() => this.setModalShow(false)} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
                  <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                      Add Address
                    </Modal.Title>
                  </Modal.Header>
                  <form className="form-group" onSubmit={this.addressSubmitHandle}>
                      <Modal.Body>
                          <div className="input-group">
                              <textarea required id="address" className="form-control"/>
                          </div>
                      </Modal.Body>
                      <Modal.Footer>
                        <input type="submit" className="btn btn-success" value="Submit"/>
                      </Modal.Footer>
                    </form>
                    </Modal>
                    </div>
       	</React.Fragment>
	}
}

export default SelectAddress