import React,{Component} from 'react'
import CheckoutForm from './CheckoutForm'
class SelectPayment extends Component
{
	constructor(props)
	{
		super(props)
	}
	componentDidMount()
	{
		console.log(this.props)
		if(!this.props.location.state)
		{
			this.props.history.push('/cart')
		}
	}
	render()
	{
		const price = this.props.location.state ? this.props.location.state.price : 0;
		const address = this.props.location.state ? this.props.location.state.address : ''
		const items = this.props.location.state ? this.props.location.state.items : []
		return (<CheckoutForm price={price} address={address} items={items}/>

			)
	}
}

export default SelectPayment