import React, { Component } from 'react'
import {Link} from 'react-router-dom'
class MenuBar extends Component {
    render() {
        return (
            <div className="bg-light mb-3">
                <div className="container">
                    <div className="row">
                        <div className="py-3 col-4 text-center">
                            <Link to="/medicines" className="text-dark text-decoration-none"><i className="fas fa-pills"></i><div className="d-none d-lg-inline d-md-inline d-sm-inline"> Medicines</div></Link>
                        </div>
                        <div className="py-3 col-4 text-center">
                            <Link to="/fitness" className="text-dark text-decoration-none"><i className="fas fa-dumbbell"></i><div className="d-none d-lg-inline d-md-inline d-sm-inline"> Fitness</div></Link>
                        </div>
                        <div className="py-3 col-4 text-center ">
                            <Link to="/personalcare" className="text-dark text-decoration-none"><i className="fas fa-user-injured"></i><div className="d-none d-lg-inline d-md-inline d-sm-inline"> Personal Care</div></Link>
                        </div>
                        </div>
                    </div>        
            </div>
        )
    }
}

export default MenuBar
