import React, { Component } from 'react'
import { Link,Redirect } from 'react-router-dom'
import StripeCheckout from 'react-stripe-checkout'
import axios from 'axios'
import Modal from 'react-bootstrap/Modal'
class CheckoutForm extends Component
{
	constructor(props)
	{
		super(props)
		this.state = {
			email : '',
			card : false,
			code : false
		}
	}
	openCard = ()=>{
		this.setState({card : true,cod : false})
	}
	openCod = ()=>{
		this.setState({card : false, cod : true})
	}
	componentDidMount()
	{
		console.log(this.props)
		axios.get('https://medicateindia.herokuapp.com/api/userData/getEmail',{
			headers:{
				'authorization':"JWT "+localStorage.token
			}
		})
		.then(res=>{
			console.log(res)
			this.setState({email:res.data})
		})
		.catch(err=>{
			if(err.response.status === 403){
				alert('Session Expired');
				localStorage.clear();
				this.setState({redirectToLogin : true})
			}
		})
	}
	cardPaymentHandle = async ()=>
	{
		const token = await this.props.stripe.createToken({email : this.state.email})
		console.log(token);
		axios.post('https://medicateindia.herokuapp.com/api/userData/stripe/payment',{
			source : token.id,
			amount : 0,
			receipt_email : this.state.email
		},{
			headers:{
				'authorization' : 'JWT '+localStorage.token
			}
		})
		.then(res=>{
			if(res.data === 'Order placed')
			{
				this.setState({placed : true})
			}
		})
		.catch(err=>{
			console.log(err.response)
		})
	}
	codPaymentHandle = ()=>{
		axios.post('https://medicateindia.herokuapp.com/api/userData/codPayment',{
			totalPrice : this.props.price,
			address : this.props.address,
			items : this.props.items
		},{
			headers:{
				'authorization' : 'JWT '+localStorage.token
			}
		})
		.then(res=>{
			console.log(res);
			this.setState({placed:true})
		})
	}
	cardPayment =  (e)=>{
		console.log(e)
		axios.post('https://medicateindia.herokuapp.com/api/userData/cardPayment',{
			totalPrice : this.props.price,
			address : this.props.address,
			items : this.props.items
		},{
			headers:{
				'authorization' : 'JWT '+localStorage.token
			}
		})
		.then(res=>{
			this.setState({placed:true})
		})
	}
	render()
	{
		if(this.state.redirectToLogin)
		{
			return <Redirect to="/login" />
		}
		if(this.state.placed)
		{
			return <Modal show={true} size="lg" onHide={()=>{}} aria-labelledby="contained-modal-title-vcenter" centered>
                  <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                      <b  style={{fontSize:"30px"}}><i className="text-success fas fa-check-circle"></i> Order Placed</b>
                    </Modal.Title>
                  </Modal.Header>
                      <Modal.Body>
                      		<div>
                      			<h3>Your Order has been placed</h3>
                      			<p>Thanks for shopping at <i className="fas fa-heartbeat"></i> Medicate</p>
                      		</div>
                      </Modal.Body>
                      <Modal.Footer>
                        <Link to="/customer/orderHistory" className="btn btn-success" value="Submit">View orders</Link>
                      </Modal.Footer>
                    </Modal>
		}
		const showCard = this.state.card ? '':'d-none'
		const showCod = this.state.cod ? '':'d-none'
		return <React.Fragment>
		<div className="container mt-2">
			<h3><b>Amount To Pay : </b>{this.props.price}</h3>
			<div className="container">
				<div class="form-check">
      				<input className="form-check-input" type="radio" onClick={this.openCard} name="paymentMode"/>
      				<label className="form-check-label"> Credit/Debit Card</label>
				</div>
					<div className={"col-10 offset-1 "+showCard}>
						{/*<div className="card" style={{height:"4rem"}}>
							<CardElement className="my-auto mx-2" style={{base: {fontSize: '20px'}}} />
						</div>
							<button className="btn btn-block btn-info col-2 offset-5 mt-2" onClick={this.cardPaymentHandle}>PAY</button>	*/}
						<StripeCheckout
          					name="Medicate"
          					amount={this.props.price*100}
          					currency="INR"	
          					stripeKey="pk_test_K52tqxznN7nYKOCUKptXJ5LW00VPxxDgo8"
          					triggerEvent="onClick"
          					token = {this.cardPayment}
        					>         
        					<button icon="cart" color="teal" floated="right" className="btn btn-lg btn-info">Checkout	</button>
        					</StripeCheckout>


					</div>
				<div class="form-check">
      				<input className="form-check-input" type="radio" name="paymentMode" onClick={this.openCod}/>
      				<label className="form-check-label">Cash On Delivery</label>
				</div>
					<div className={"col-10 offset-1 "+showCod}>
							<button className="btn btn-block btn-info col-3 mt-2" onClick={this.codPaymentHandle}>CONFIRM ORDER</button>	
					</div>
			</div>
			
		</div>
		</React.Fragment>
	}
}
export default (CheckoutForm)