import React , {Component} from 'react';
import {Link} from 'react-router-dom'
class Footer extends Component
{
	render()
	{
		return <footer id="footer" style={{marginTop : "20em"}} className=" bg-dark text-light pt-4">
  					<div className="container-fluid text-center text-md-left">
    					<div className="row">
      						<div className="col-md-6 mt-md-0 mt-3">
        						<h4><i className="fas fa-heartbeat"/> Medicate</h4>
        						<p style={{fontSize:"12px"}}>At Medicate, we ensure that you get high-quality life-saving medicines are delivered to you on time. Order medicines online at your convenience from across the country. We also deliver Ayurvedic, Homeopathic, Unani and Over-The-Counter (OTC) products !</p>
      						</div>
      						<hr className="clearfix w-100 d-md-none pb-3"/>
      						<div className="col-md-3 mb-md-0 mb-3">
        						<ul className="list-unstyled">
        						<h5>USER</h5>
						       		<li>
						           		<Link to="/login" className="text-decoration-none text-light">Log In</Link>
						          	</li>
						          	<li>
						            	<Link to="/signup" className="text-decoration-none text-light">Sign Up</Link>
						          	</li>
        							</ul>
      						</div>
      						<div className="col-md-3 mb-md-0 mb-3">
        						<h5>SHOP</h5>
        						<ul className="list-unstyled">
          							<li>
            							<Link to="/medicine" className="text-decoration-none text-light">Medicines</Link>
          							</li>
          							<li>
            							<Link to="fiteness" className="text-decoration-none text-light">Fitness</Link>
          							</li>
          							<li>
            							<Link to="/personalcare" className="text-decoration-none text-light">Personal Care</Link>
          							</li>
        						</ul>
      						</div>
    					</div>
  					</div>
  				<div className="footer-copyright text-center py-3"><i className="far fa-copyright"></i> 2020 Copyright:
    				<Link to="/" className="text-decoration-none text-light"> <i className="fas fa-heartbeat"/> Medicate</Link>
  				</div>
			</footer>
		}
}
export default Footer