import React,{Component} from 'react';
import {Link} from 'react-router-dom'
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class Admin extends Component {
	constructor(props)
	{
		super(props)
		this.state = {
			verified : false,
			showAlert : false,
            loading : true
		}
		this.alertBox = React.createRef();

	}
	componentDidMount()
	{
		document.getElementById('navbar').style.display="none"
        document.getElementById('footer').style.display="none"
        if(sessionStorage.token)
        {
            this.props.history.push('/adminPanel')
        }
        this.setState({loading:false})
	}
	submitHandle = (event)=>{
		event.preventDefault();
        this.setState({loading:true})
        axios.post('https://medicateindia.herokuapp.com/api/admin/login',{
            username : event.target.email.value,
            password : event.target.pass.value
        })
        .then(res=>{
            if(res.data.token)
            {
                sessionStorage.setItem('token',res.data.token);
                this.props.history.push('/adminpanel')
            }
            else{
                this.setState({showAlert:true,alertMessage:res.data,loading:false})
            }
        })
	}
	alertHandle = ()=>{
		this.setState({showAlert:!this.state.showAlert})
	}
	render()
	{
		const alertShow = this.state.showAlert ? 'd-block' : 'd-none'
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
		return(<React.Fragment>
			<div className="p-3 logo mr-auto ml-auto ">
                        <Link to="/" className="text-decoration-none"><h1 className="text-center text-light" styled="font-family:'Krona One'"><i className="fas fa-heartbeat"></i> Medicate</h1>
                        <h6 className="text-center text-light" style={{fontFamily: 'Roboto'}}>Health aapki, fikar hamari</h6></Link>
                    </div>

                    <div className="container bg-white p-5 my-5">
                    <div className="row">
                            <div className="offset-md-3 offset-lg-3 offset-sm-2 col-md-6 col-lg-6 col-sm-6">
                                <h3 className="text-center" ><b>ADMINISTRATOR</b></h3>
                            	<h6 className="text-center" ><b>Please sign in to get access.</b></h6>
                            </div>
                    </div>
                    <div className='row' className={alertShow}>
                    	<div className="offset-md-4 offset-lg-4 offset-sm-1 col-md-4 col-lg-4 col-sm-4">
	                        <div className="alert alert-danger alert-dismissible">
	  							<button type="button" className="close" onClick={this.alertHandle}>&times;</button>
	    						<strong> {this.state.alertMessage}</strong>
	  						</div>
  						</div>
  					</div>
                        <form onSubmit={this.submitHandle}>
                            <div className="form-row mt-3">
                                <div className="input-group offset-md-4 offset-sm-1 offset-lg-4 col-md-4 col-lg-4 col-sm-4">
                                    <div className="input-group-prepend">
                                    	<span className="input-group-text"><i className="fas fa-user"></i></span>
                                    </div>
                                    <input required className="form-control" placeholder="Username" autoFocus id="email"/>
                                </div>
                            </div>
                            <div className="form-row mt-3">
                                <div className="input-group  offset-md-4 offset-sm-1 offset-lg-4 col-md-4 col-lg-4 col-sm-4">
                                    <div className="input-group-prepend">
                                    	<span className="input-group-text"><i className="fas fa-key"></i></span>
                                    </div>
                                    <input required className="form-control" placeholder="Password" type="password" id="pass"/>
                                </div>
                            </div>
                            <div className="form-row mt-5">
                                <div className=" offset-md-4 offset-sm-1 offset-lg-4 col-md-4 col-lg-4 col-sm-4">
                                    <input type="submit" value="Sign In" id="sub-btn" className="btn btn-block btn-lg btn-success text-white"/>
                                </div>
                            </div>
                        </form>
                    </div>
                    </React.Fragment>
			)
	}
}

export default Admin;