import React,{Component} from 'react'
import Carousel from './Carousel';
import MenuBar from './MenuBar';
import {Link} from 'react-router-dom'
class Home extends Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			crouselImages : ['Group62.png','carousel1.jpg','carousel5.jpg'],
			brands : [{img:"./Home/gsk.png",link:"Glaxo Smith Kline"},
					{img:"./Home/nexium.png",link:"Nexium"},
					{img:"./Home/Gilead_Sciences.png",link:"Gilead Sciences"},
					{img:"./Home/benadryl.png",link:"Benadryl"},
					{img:"./Home/roche.png",link:"Roche"},
					{img:"./Home/pfizer.png",link:"Pfizer"},
					{img:"./Home/novartis.png",link:"Novartis"},
					{img:"./Home/johnson.png",link:"Johnson & Johnson"},
					{img:"./Home/abbott.jpg",link:"Abbott"},
					{img:"./Home/Mitsubishi_Tanabe_Pharma.jpg",link:"Mitsubishi Tanabe Pharma"}]
		}
	}
    render()
    {
        return <React.Fragment>
                <MenuBar/>
                <Carousel images={this.state.crouselImages}/>
                <div className="container-fluid mt-5">
                <div className="text-center ">
                    <h2 style={{fontFamily:"Libre Baskerville"}}><b>Shop By Top Brands</b></h2>
                    <hr/>
                    <div className="row mb-5 mt-5">
                    {
                        this.state.brands.map((item,index)=>{
                            return <Link className="card mx-auto mb-5" key={index} to={'/shopByTopBrands/'+item.link}> 
                                        <div className="my-auto"  style={{width: "14rem"}}>
                                            <img alt={item.img} className="p-3 card-img-top" src={item.img}/>
                                        </div>
                                    </Link>
                        })
                    }
                    </div>
                    </div>
                </div>
        </React.Fragment>
    }
}

export default Home;