import React , {Component} from 'react';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class AdminDetails extends Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			users : 0,
			products : 0,
			loading : true
		}
	}
	componentDidMount(){
		axios.get('https://medicateindia.herokuapp.com/api/adminProtected/count',{
			headers : {
				'authorization' : "JWT " + sessionStorage.token
			}
		})
		.then(res=>{
			console.log(res);
			this.setState({users:res.data.users,products:res.data.products,earnings:res.data.earnings,loading:false})
		})
		.catch(err=>{
			console.error(err);
		})
	}
	render()
	{
		if(this.state.loading)
		{
			return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
		}
		return(
				<React.Fragment>
					<div className="container">
						<div className="row d-flex justify-content-center">
							<div className="card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-5">
							    			<h5 className="card-title">{this.state.users}</h5>
							    			<h6 className="card-subtitle text-muted">Users</h6>
							    		</div>
							    		<div className="offset-3">
							    			<h5><i className="fas fa-user text-dark" style={{fontSize:"40px"}}></i></h5>
							    		</div>
							    	</div> 
							  	</div>
							</div>
							<div className="offset-sm-1 offset-lg-1 offset-md-1 card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-5">
											<h5 className="card-title">{this.state.products}</h5>
							    			<h6 className="card-subtitle text-muted">Medicine</h6>
							    		</div>
							    		<div className="offset-3">
							    			<h5><i className="fas fa-shopping-cart text-dark" style={{fontSize:"40px"}}></i></h5>
							    		</div>
							    	</div> 
							  	</div>
							</div>
							<div className="offset-sm-1 offset-lg-1 offset-md-1 card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-9">
							    			<h5 className="card-title">Rs. {this.state.earnings}</h5>
							    			<h6 className="card-subtitle text-muted">Earnings</h6>
							    		</div>
							    			<h5><i className="fas fa-wallet text-dark" style={{fontSize:"40px"}}></i></h5>
							    	
							    	</div> 
							  	</div>
							</div>
						</div>
					</div>
				</React.Fragment>
			)
	}
}

export default AdminDetails