import React, { Component } from 'react'
import {Link, withRouter} from'react-router-dom'
class Navbar extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            menu:false
        }
    }
    toggler = ()=>
    {
        this.setState({menu : !this.state.menu});
    }
    render() {
        const userLink = (
                        <React.Fragment>
                            <li className="nav-item m-3">
                                <Link className="nav-link" to="/account"><i className="fas fa-user"></i> Profile</Link>
                            </li>
                            <li className="nav-item m-3">
                                <Link className="nav-link" to="/cart"><i className="fas fa-shopping-cart"></i></Link>
                            </li>
                        </React.Fragment>
        )
        const loginLink = (
                        <li className="nav-item m-3">
                            <Link className="nav-link" to="/login"><i className="fas fa-user"></i> Login</Link>
                        </li>)
        const show = this.state.menu ? "show" :  "none";
        return (
            <nav id="navbar" className="navbar navbar-expand-md navbar-dark p-1 Homepage-navbar">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">
                        <div className="logo mr-auto ml-2 mt-2">
                            <h2 className="text-center text-light" ><i className="fas fa-heartbeat"></i> Medicate</h2>
                            <p style={{fontSize : "60%"}} className="text-center text-light" >Health aapki, fikar hamari</p>
                        </div>
                    </Link>
                <button className="navbar-toggler mr-2" type="button" onClick={this.toggler}>
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className={"collapse navbar-collapse  "+show} >
                    <ul className="navbar-nav container">
                    <li className="nav-item m-3 col-9">
                            <form action="/search">
                                <div className="input-group">
                                    <input className="form-control" name="q" placeholder="Search"></input>
                                    <div className="input-group-append">
                                        <button type="submit" className="btn btn-success"><i className="fas fa-search"></i></button>
                                    </div>
                                </div>
                            </form>
                        </li>
                        {localStorage.token ? userLink : loginLink}
                        
                    </ul>
                </div>
                </div>
          </nav>
        )
    }
}

export default withRouter(Navbar)
