import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import Spinner from 'react-bootstrap/spinner'
import queryString from 'query-string'
import $ from 'jquery'
import PageNotFound from './PageNotFound'

class Search extends Component
{
	constructor(props)
	{
		super(props)
		this.state = {
			query : queryString.parse(this.props.location.search).q,
            items : [],
            loading : true,
            start : 1,
            allFetched : false,
            LoadingMore : false,
            sort : 'relevance'
        }
	}
	componentDidMount()
	{
		this.fetchProducts();
	}
    fetchProducts = async ()=>{
        // d7hjdvye4sky5cdwmmmtf3bf
        // bxktfmp48ryvgntbpwx3ybcz
        // 1085666
        let ct = 0;
	        await axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.state.query}&categoryId=1085666&start=${this.state.start}&sort=${this.state.sort}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
	        .then(res=>{
	        	if(res.data.message !== "Results not found!"){
		        	let items = this.state.items
		        	items = items.concat(res.data.items);
		            this.setState({items : items,loading:false});
                    ct = 1;
	            }
	        })
	        .catch(err=>{
	            console.error(err);
	        });
	        await axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.state.query}&categoryId=976760&start=${this.state.start}&sort=${this.state.sort}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
	        .then(res=>{
	        	if(res.data.message !== "Results not found!"){
		       		let items = this.state.items
		        	items = items.concat(res.data.items);
		            this.setState({items : items,loading:false});
                    ct = 1
	            }
	        })
	        .catch(err=>{
	            console.error(err);
	        });
            if(ct === 0)
            {
                this.setState({noResultFound:true,LoadingMore:false,loading:false})
            }
    }
    fetchMore = async ()=>{
        var ct = 0;
	        await axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.state.query}&categoryId=1085666_136815&start=${this.state.start}&sort=${this.state.sort}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
	        .then(res=>{
	        	if(res.data.message !== "Results not found!"){
		        	let items = this.state.items
		        	items = items.concat(res.data.items);
		            this.setState({items : items,LoadingMore:false});
                    ct = 1
	        }
	        })
	        .catch(err=>{
	            console.error(err);
	        });
	        await axios.get(`https://cors-anywhere.herokuapp.com/http://api.walmartlabs.com/v1/search?query=${this.state.query}&categoryId=976760&start=${this.state.start}&sort=${this.state.sort}&numItems=12&format=json&apiKey=4v8v4rm827cdkeze2ekd6zgf`)
	        .then(res=>{
	        	if(res.data.message !== "Results not found!"){
		       		let items = this.state.items
		        	items = items.concat(res.data.items);
		            this.setState({items : items,LoadingMore:false});
                    ct = 1
	            }
	        })
	        .catch(err=>{
	            console.error(err);
	        });
            if(ct === 0)
            {
                this.setState({NotFound:true,LoadingMore:false})
            }   
    }
    componentDidUpdate()
    {
        $(window).scroll(()=> 
        {
            console.log($(window).scrollTop(),$(document).height() - $(window).height()-200)
            if(($(window).scrollTop() === ($(document).height() - $(window).height())) && (!this.state.LoadingMore && !this.state.allFetched)) 
            {
                this.setState({LoadingMore:true})
                let start = this.state.start;
                this.setState({start:start+12});
                this.fetchMore()
            }
        })
    }
    selectHandle = (event)=>{
        this.setState({sort:event.target.value,start:1,allFetched : false,LoadingMore : false,})
        setTimeout(()=>this.fetchProducts());
    }
    render() {
    	if(this.state.noResultFound)
    	{
    		return (
    			<React.Fragment>
    				<div className="d-flex justify-content-center display-4 mt-5"><b>No Results Found</b></div>
    			</React.Fragment>
    			)
    	}
        const showLoad = this.state.LoadingMore ? "d-flex justify-content-center" : "d-none"
        const showEnd = this.state.allFetched ? "d-flex justify-content-center" : "d-none"
        if(this.state.err)
        {
            return <PageNotFound/>
        }
        if(this.state.loading)
        {
            return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
        return (
            <div>
                <div className="container-fluid mt-3 ">
                    <div className="d-flex justify-content-center">
                        <h1 className="text-capitalize"><b>{this.props.match.params.item}</b></h1>
                        
                    </div>
                    <hr/>
                    <div className="container mt-2 mx-auto">
                        <div className="row">
                            {
                                this.state.items.map((item,index)=>{
                                    return (
                                    <div className="item card col-10 col-sm-5 col-md-3 col-lg-2 col-xs-10 mb-1" key={index}>
                                        <Link style={{textDecoration:"none"}} className="text-dark" to={"/item/"+item.itemId}>
                                        <img className="card-img-top m-2" src={item.mediumImage} style={{height:"8rem"}} alt={item.mediumImage}/>
                                        <div className="card-body">
                                        <h6 title={item.name} style={{height:"5rem"}} className="overflow-hidden card-title">{item.name}</h6>
                                        <p className="card-text"><b><i className="fas fa-rupee-sign"/> {(Number(item.salePrice)*71.29).toFixed(2)}</b></p>
                                        </div>
                                        </Link>
                                    </div>
                                    )
                                })
                            }
                        </div>
                        <div className={"mt-4 mb-4 "+showLoad}>
                            <Spinner animation="border" role="status" variant="primary">
                              <span className="sr-only">Loading...</span>
                            </Spinner>
                        </div>
                        <div className={"mt-4 mb-4 "+showEnd}>
                             <h3><i className="fas fa-heart-broken"/> OOF! You've reached the end!</h3>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Search