import React , {Component} from 'react';
import {Link} from 'react-router-dom'
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class UserDetails extends Component {
	constructor(props)
    {
        super(props);
        this.state = {
        	loading : true
        }
    }
    componentDidMount()
    {
        document.getElementById('navbar').style.display="none"
        document.getElementById('footer').style.display="none"
        if(!sessionStorage.token)
        {
            sessionStorage.clear()
            this.props.history.push('/admin');
        }
        else{
        	this.fetchUserDetails();
        }
    }
    fetchUserDetails = ()=>{
    	console.log(this.props)
    	axios.post('https://medicateindia.herokuapp.com/api/adminProtected/userDetails',{
    		id : this.props.match.params.id
    	},{
    		headers : {
    			'authorization' : "JWT "+sessionStorage.token
    		}
    	})
    	.then(res=>{
    		console.log(res.data)
    		this.setState({user:res.data,loading:false})
    	})
    	.catch(err=>{
    		if(err.response && err.response.status === 403)
    		{
    			sessionStorage.clear()
    			alert(err)
    			this.props.history.push('/admin')
    		}
    		else{
    			alert('Something Went Wrong')
    			this.props.history.push('/admin')
    		}
    	})
    }
    render() {
    	if(this.state.loading)
		{
			 return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
		}
    	return(
			<React.Fragment>
                <nav className=" navbar navbar-expand-md navbar-dark Homepage-navbar">
                    <Link className="navbar-brand ml-3" to="/adminpanel">
                        <div className="logo">
                            <h4 className="text-center text-light" ><i className="fas fa-heartbeat"></i> Medicate</h4>
                            <p style={{fontSize:"10px"}} className="text-center text-light" >Health aapki, fikar hamari</p>
                        </div>
                    </Link>
	                    <ul className="navbar-nav ml-3 ml-md-auto ml-lg-auto ml-sm-auto mr-3">
	                        <li className="nav-item my-3">
	                            <button className="nav-link bg-transparent border-0" style={{outline:0}} onClick={this.props.logout}><div className="d-none d-sm-inline d-md-inline d-lg-inline">Log Out &nbsp;</div><i className="fas fa-sign-out-alt"></i> </button>
	                        </li>
	                    </ul>
          		</nav>
          		<div className="container">
						<div className="row d-flex justify-content-center">
							<div className="card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-12">
							    			<h4 className="card-title"><b>{this.state.user.Fname + " "+this.state.user.Lname}</b></h4>
							    			<i>{this.state.user.email}</i><br/>
							    			<i>+91- {this.state.user.mobile}</i>
							    		</div>
							    	</div> 
							  	</div>
							</div>
							<div className="offset-sm-1 offset-lg-1 offset-md-1 card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-5">
											<h5 className="card-title">{this.state.user.cart.length}</h5>
							    			<h6 className="card-subtitle text-muted">In Cart</h6>
							    		</div>
							    		<div className="offset-3">
							    			<h5><i className="fas fa-shopping-cart text-dark" style={{fontSize:"40px"}}></i></h5>
							    		</div>
							    	</div> 
							  	</div>
							</div>
							<div className="offset-sm-1 offset-lg-1 offset-md-1 card mt-2" style={{width: "15rem"}}>
							  	<div className="card-body">
							  		<div className="row">
							  			<div className="col-5">
							    			<h5 className="card-title">{this.state.user.orderHistory.length}</h5>
							    			<h6 className="card-subtitle text-muted">Orders</h6>
							    		</div>
							    		<div className="offset-3">
							    			<h5><i className="fas fa-wallet text-dark" style={{fontSize:"40px"}}></i></h5>
							    		</div>
							    	</div> 
							  	</div>
							</div>
						</div>
					</div>
          			
            </React.Fragment>
			)
	}
}

export default UserDetails