import React from 'react';
import {Link,Redirect} from 'react-router-dom';
import axios from 'axios';
import Modal from 'react-bootstrap/Modal'
import Spinner from 'react-bootstrap/Spinner'
class SignUp extends React.Component
{
    constructor()
    {
        super();
        this.state = {
            FName:"",
            LName:"",
            email:"",
            pass:"",
            mobile:"",
            verified:false,
            submitted : false,
            redirect : false,
            resending : false,
            sent : false,
            loading : true,
            disableButton : false,
            showModal : true
        }
        this.FirstNameRef = React.createRef();
        this.LastNameRef = React.createRef();
        this.equalEmailRef = React.createRef();
        this.passConstRef = React.createRef();
        this.letterRef = React.createRef();
        this.capitalRef = React.createRef();
        this.passNumberRef = React.createRef();
        this.passLengthRef = React.createRef();
        this.mobileLengthRef = React.createRef();
        this.passConfirmRef = React.createRef();
        this.confirmInputRef = React.createRef();
    }
    checkEmail = (event)=>
    {
        this.equalEmailRef.current.style.display = 'block';
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(event.target.value))
        {
            this.equalEmailRef.current.classList.remove('invalid');
            this.equalEmailRef.current.classList.add('valid');
        }
        else
        {
            this.equalEmailRef.current.classList.remove('valid');
            this.equalEmailRef.current.classList.add('invalid');
        }
        this.setState({email:event.target.value});
    }
    checkPass = (event)=>
    {
        this.setState({pass:event.target.value});
        this.passConstRef.current.style.display='block';
        var lowerCaseLetters = /[a-z]/g;
        if(event.target.value.match(lowerCaseLetters)) 
        {  
            this.letterRef.current.classList.remove("invalid");
            this.letterRef.current.classList.add("valid");
        } 
        else 
        {
            this.letterRef.current.classList.remove("valid");
            this.letterRef.current.classList.add("invalid");
        }
        var upperCaseLetters = /[A-Z]/g;
        if(event.target.value.match(upperCaseLetters)) 
        {  
            this.capitalRef.current.classList.remove("invalid");
            this.capitalRef.current.classList.add("valid");
        } 
        else 
        {
            this.capitalRef.current.classList.remove("valid");
            this.capitalRef.current.classList.add("invalid");
        }
        var numbers = /[0-9]/g;
        if(event.target.value.match(numbers)) 
        {  
            this.passNumberRef.current.classList.remove("invalid");
            this.passNumberRef.current.classList.add("valid");
        } 
        else 
        {
            this.passNumberRef.current.classList.remove("valid");
            this.passNumberRef.current.classList.add("invalid");
        }
        
        if(event.target.value.length >= 8) 
        {
            this.passLengthRef.current.classList.remove("invalid");
            this.passLengthRef.current.classList.add("valid");
        } 
        else 
        {
            this.passLengthRef.current.classList.remove("valid");
            this.passLengthRef.current.classList.add("invalid");
        }
        if(this.confirmInputRef.current.value === event.target.value && this.state.pass!=="")
        {
            this.passConfirmRef.current.classList.remove('invalid');
            this.passConfirmRef.current.classList.add('valid');
        }
        else
        {
            this.passConfirmRef.current.classList.add('invalid');
            this.passConfirmRef.current.classList.remove('valid');
        }
    }
    checkConfirm = (event)=>
    {
        if(this.state.pass === event.target.value && this.state.pass!=="")
        {
            this.passConfirmRef.current.classList.remove('invalid');
            this.passConfirmRef.current.classList.add('valid');
        }
        else
        {
            this.passConfirmRef.current.classList.add('invalid');
            this.passConfirmRef.current.classList.remove('valid');
        }
    }
    checkMobile = (event)=>
    {
        this.mobileLengthRef.current.style.display='block';
        if(event.target.value.length === 10)
        {
            this.mobileLengthRef.current.classList.remove('invalid');
            this.mobileLengthRef.current.classList.add('valid');
        }
        else
        {
            this.mobileLengthRef.current.classList.remove('valid');
            this.mobileLengthRef.current.classList.add('invalid');
        }
        this.setState({mobile:event.target.value});
    }
    handleSubmit = (event)=>{
        event.preventDefault();
        this.setState({FName : this.FirstNameRef.current.value,
                        LName : this.LastNameRef.current.value,loading:true})
        if(this.capitalRef.current.classList.contains('valid') && 
        this.equalEmailRef.current.classList.contains('valid') && 
        this.passNumberRef.current.classList.contains('valid') && 
        this.passLengthRef.current.classList.contains('valid') && 
        this.passConfirmRef.current.classList.contains('valid') &&
        this.mobileLengthRef.current.classList.contains('valid'))
        {
            axios.post('https://medicateindia.herokuapp.com/api/user/verify',{email:this.state.email})
                .then((res)=>{
                    if(res.data === 'OK')
                    {
                        this.setState({verified:true,loading:false})
                    }
                    else
                    {
                        alert('User Already Exists');
                    }
                })
                .catch(function(err){
                    console.log(err);
                })
        }
        else
        {
            alert('Fill Form Correctly');
        }
    }

    verifyOTP = (event)=>{
        event.preventDefault()
        this.setState({loading:true})
        axios.post('https://medicateindia.herokuapp.com/api/user/validateotp',{
            inputOtp: event.target.otp.value,
            Fname : this.state.FName,
            Lname : this.state.LName,
            email : this.state.email,
            pass : this.state.pass,
            mobile : this.state.mobile
        })
        .then((response)=>{
            console.log(response)
            if(response.data === 'submitted')
            {
                this.setState({submitted:true,loading:false})
            }
            else 
            {
                alert('Incorrect');
                this.setState({loading:false})
            }
        })
    }

    resendOTP = ()=>{
        this.setState({resending:true,sent:false,disableButton:true})
        axios.post('https://medicateindia.herokuapp.com/api/user/resendotp',{email:this.state.email})
        .then(res=>{
            if(res.data === 'OK')
            {
                this.setState({sent : true,resending:false,disableButton:false})
            }
            else{
                alert('Something went wrong!')
            }
        })
                .catch(function(err){
                    console.log(err);
                })
    }
    redirectHandle = ()=>{
        this.setState({redirect : true});
    }
    componentDidMount(){
        if(localStorage.token)
        {
            this.props.history.push('/');
        }
        this.setState({loading:false})
    }
    render()
    {
        if(this.state.loading)
        {
                        return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
        }
        if(this.state.redirect)
        {
            return <Redirect to="/login" />
        }
        if(this.state.submitted)
        {
            return (
                <Modal show={this.state.showModal} size="lg" onHide={this.redirectHandle} aria-labelledby="contained-modal-title-vcenter" centered>
                  <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                      <b  style={{fontSize:"30px"}}><i className="text-success fas fa-check-circle"></i> Account Registered</b>
                    </Modal.Title>
                  </Modal.Header>
                      <Modal.Body>
                              <div>
                                  <h3>Your account has been created successfully...</h3>
                                  <p>Thanks for choosing <i className="fas fa-heartbeat"></i> <b>Medicate</b></p>
                              </div>
                      </Modal.Body>
                      <Modal.Footer>
                        <Link to="/login" className="btn btn-primary" value="Submit">Log In</Link>
                      </Modal.Footer>
                    </Modal>
                )
        }
        else if(this.state.verified)
        {
            const showsending = this.state.resending ? 'd-inline' : 'd-none';
            const showsent = this.state.sent ? 'd-inline' : 'd-none';
            return <React.Fragment>
                        <div className="container mt-4">
                            <div className="row">
                                <div className="offset-md-3 offset-lg-3 offset-sm-3 col-md-6 col-lg-6 col-sm-6">
                                    <h5 styled="font-family: 'Karona One'">Validate OTP (One Time Passcode)</h5>
                                <hr/>
                                    A One Time Passocode has been sent at <b>{this.state.email}</b><br/><br/>
                                    Please enter the OTP below to verify your Email Address. If you cannot see
                                    the email from "Medicate India" in your inbox, make sure to check your SPAM folder.
                                    
                                    <form onSubmit={this.verifyOTP} className="mt-2">
                                        <div className="form-group form-row">
                                        <h6 className={"text-danger "+showsending}>Sending OTP...</h6>
                                        <h6 className={"text-success "+showsent}>OTP Sent</h6>
                                            <input className="form-control " id='otp' placeholder="Enter OTP"></input>

                                        </div>
                                        <button type="submit" className="btn btn-primary btn-sm">Validate OTP</button>
                                        <button type="button" className="float-right btn btn-sm btn-secondary" disabled={this.state.disableButton} onClick={this.resendOTP}>Resend OTP</button>
                                        
                                    </form>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
        }
        return <React.Fragment>
                    <div className="container mt-4">
                        <div className="row">
                            <div className="offset-md-3 offset-lg-3 offset-sm-3 col-md-6 col-lg-6 col-sm-6">
                                <h3 styled="font-family: 'Karona One'">Create a New Account</h3>
                            <hr/>
                        </div>
                    </div>
                    <form onSubmit={this.handleSubmit}>
                        <div className="row form-group">
                            <div className="offset-md-3 offset-lg-3 offset-sm-3 col-md-3 col-lg-3 col-sm-3">
                                <label htmlFor="Fname">First Name :</label>
                                <input required className="form-control" ref={this.FirstNameRef} autoFocus type="text" id="Fname"/>
                            </div>
                            <div className="col-md-3 col-lg-3 col-sm-3">
                                <label htmlFor="Lname">Last Name :</label>
                                <input required className="form-control" type="text" id="Lname" ref={this.LastNameRef}/>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="offset-md-3 offset-lg-3 offset-sm-3 col-md-6 col-lg-6 col-sm-6">
                                <label htmlFor="sign-up-email">Email :</label>
                                <div className='input-group'>
                                    <input required className="form-control" onChange={this.checkEmail} placeholder="abcd@example.com" type="text"  id="sign-up-email"/>
                                        <div className="input-group-append border-left-0">
                                            <span className="input-group-text bg-white" id="equal-email" ref={this.equalEmailRef}></span>
                                        </div>
                                </div>
                                <small id='message'>We'll never share your email with anyone else.</small>
                            </div>
                        </div>
                        <div className="form group row mt-2">
                            <div className="offset-md-3 offset-lg-3 offset-sm-3 col-md-3 col-lg-3 col-sm-3">
                                <label htmlFor="sign-up-pass">Password :</label>
                                <input required className="form-control" onChange={this.checkPass} type="password" id="sign-up-pass"/>
                            </div>
                            <div className="col-md-3 col-lg-3 col-sm-3">
                                <label htmlFor="sign-up-confirm">Confirm :</label>
                                <input required className="form-control" type="password" ref={this.confirmInputRef} onChange={this.checkConfirm} id="sign-up-confirm"/>
                            </div>
                        </div>
                        <div className="row form-group mt-2">
                            <div className="col-md-5 col-lg-5 col-sm-5 offset-sm-3 offset-lg-3 offset-md-3" ref={this.passConstRef} id="password-constraint">
                                
                                <div id="letter" className="invalid" ref={this.letterRef}> A <b>lowercase</b> letter</div>
                                <div id="capital" className="invalid" ref={this.capitalRef}> A <b>uppercase</b> letter</div>
                                <div id="number" className="invalid" ref={this.passNumberRef}> A <b>number</b></div>
                              <div id="Passlength" className="invalid" ref={this.passLengthRef}> Minimum <b>8 characters</b></div>
                               <div id="equal" className="invalid" ref={this.passConfirmRef}> Match</div>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="offset-md-3 offset-lg-3 offset-sm-3 col.current-md-6 col-lg-6 col-sm-6">
                                <label htmlFor="sign-up-email">Mobile :</label>
                                <div className='input-group'>
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">+91-</span>
                                    </div>
                                        <input required className="form-control" type="number" onChange={this.checkMobile} id="sign-up-mobile"/>
                                        <div className="input-group-append">
                                            <span className="input-group-text bg-white"  ref={this.mobileLengthRef} id="equal-mobile"></span>
                                        </div>
                                    </div>
                            </div>
                        </div>
                        <div className="form-group row mt-3">
                            <div className="offset-3 col-7 w-5">
                                <input className="btn btn-lg submit text-white offset-4" id="sign-up-submit" value="Sign Up" type="submit"/>
                            </div>
                        </div>
                        <hr/>
                    </form>
                </div>
            </React.Fragment>
    }
}

export default SignUp;