import React, {Component} from 'react';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
class Users extends Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			products : [],
			loading : true
		}
	}
	fetchProducts = ()=>{
		axios.get('https://medicateindia.herokuapp.com/api/adminProtected/medicines',{
			headers : {
				'authorization' : "JWT "+sessionStorage.token
			}
		})
		.then((res)=>{
			this.setState({products:res.data,loading : false})
		})
		.catch(function(err){
			console.error(err);
		})
	}
	componentDidMount(){
		if(!sessionStorage.token)
		{
			this.props.history.push('/admin')
		}
		this.fetchProducts();
    }
    handleSubmit = (event)=>{
        event.preventDefault();
        this.setState({loading : true})
        axios.post('https://medicateindia.herokuapp.com/api/adminProtected/addProduct',{
            type:event.target.type.value
		},{
			headers : {
				'authorization' : "JWT "+sessionStorage.token
			}
		})
		.then(res=>{
			if(res.data === 'exist')
			{
				alert('Product with same Type Already Exists')
			}
			else{
				this.fetchProducts();
			}
		})
        .catch(err=>{
            console.error(err);
        })
		
	}
	handleDelete = (type)=>{
		this.setState({loading : true})
		axios.post('https://medicateindia.herokuapp.com/api/adminProtected/deleteProduct',{
			type : type
		},{
			headers : {
				'authorization' : "JWT "+sessionStorage.token
			}
		})
		.then(res=>{
			console.log(res);
			if(res.data === "Deleted")
			{
				console.log("called delete");
				this.fetchProducts()
			}
			else{
				alert('Something Went Wrong')
			}
		})
		.catch(err=>console.error(err))
	}
	render()
	{
		if(this.state.loading)
		{
			return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
		}
		return(
			<React.Fragment>
			<div className="container">
            <h4 className="mt-2 mb-2">Add a new category</h4>
                        <form onSubmit={this.handleSubmit} className="mb-2">
                            <div className="form-row">
                                <input className="form-control col-5" required id="type" placeholder="Enter product type"></input>
                                <input className="btn  btn-success offset-1" type='submit' value="Add"></input>
                            </div>
                        </form>
				<table className="table table-light mt-2 table-responsive-sm table-responsive-lg table-responsive-md table-hover">
					<thead className="thead-dark">
                        
						<tr>
							<th>Name</th>
							<th>Available</th>
							<th>Options</th>
						</tr>
					</thead>
					<tbody>
						{this.state.products.map((product,index)=>{
							return <tr key={index}>
									<td>{product.type}</td>
									<td>{product.isAvailable ? 'Yes' : 'No'}</td>
									<td><button className="btn btn-danger" onClick={()=>this.handleDelete(product.type)}>Delete</button></td>
							</tr>
								
						})}
					</tbody>
				</table>
			</div>
			</React.Fragment>
			)
	}
}
export default  Users;