import React, {Component} from 'react';
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
import {Link} from 'react-router-dom'
class Users extends Component
{
	constructor(props)
	{
		super(props);
		this.state ={
			users : [],
			loading:true
		}
	}
	fetchUsers = ()=>{
		axios.get('https://medicateindia.herokuapp.com/api/adminProtected/userData',{
			headers : {
				'authorization' : "JWT " + sessionStorage.token
			}
		})
		.then((res)=>{
			this.setState({users:res.data,loading:false})
		})
		.catch(function(err){
			if(err.response.status === 403)
			{
				sessionStorage.clear()
				alert('Login Again')
			}
		})
	}
	componentDidMount(){
		if(!sessionStorage.token)
		{
			sessionStorage.clear()
			this.props.history.push('/admin');
		}
		else{
			this.fetchUsers()
		}
	}
	blockUser = (email)=>{
		this.setState({loading : true})
		axios.post("https://medicateindia.herokuapp.com/api/adminProtected/blockUser",{
			email : email
		},{
			headers : {
				'authorization' : "JWT "+sessionStorage.token
			}
		})
		.then(res=>{
			if(res.data === 'Blocked')
			{
				alert('User Blocked');
				this.fetchUsers();
			}
		})
		.catch(err=>{
			console.log(err)
		})
	}

	unBlockUser = (email)=>{
		this.setState({loading : true})
		axios.post("https://medicateindia.herokuapp.com/api/adminProtected/unBlockUser",{
			email : email
		},{
			headers : {
				'authorization' : "JWT "+sessionStorage.token
			}
		})
		.then(res=>{
			if(res.data === 'Blocked')
			{
				alert('User Unblocked');
				this.fetchUsers();
			}
		})
		.catch(err=>{
			console.log(err)
		})	
	}

	render()
	{
		if(this.state.loading)
		{
			 return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
		}
		return(
			<React.Fragment>
			<div className="container">
				<table className="table table-light mt-2 table-responsive-sm table-responsive-lg table-responsive-md table-hover">
					<thead className="thead-dark">
						<tr>
							<th>Name</th>
							<th>Email</th>
							<th>Contact</th>
							<th>Options</th>
						</tr>
					</thead>
					<tbody>
						{this.state.users.map((user,index)=>{
							console.log(user)
							return <tr key={index}>
										<td>{user.Fname+' '+user.Lname}</td>	
										<td>{user.email}</td>
										<td>{user.mobile}</td>
										<td>{user.isBlocked ? <button className="btn btn-success" onClick={()=>this.unBlockUser(user.email)}>Unblock</button> : <button className="btn btn-danger" onClick={()=>this.blockUser(user.email)}>Block</button>}
										<Link to={"/user/"+user._id} className="ml-2 btn btn-info text-decoration-none">Details</Link></td>
									</tr>
								
						})}
					</tbody>
				</table>
			</div>
			</React.Fragment>
			)
	}
}
export default  Users;