import React, { Component } from 'react'
import {Link,Redirect} from 'react-router-dom'
import axios from 'axios'
import Spinner from 'react-bootstrap/Spinner'
import Modal from 'react-bootstrap/Modal'

class User extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            token : localStorage.getItem('token'),
            redirectToLogin : false,
            loading:true,
            user : {},
            modalShow : false
        }
    }
    setModalShow = (a)=>{
        this.setState({modalShow : a})
    }
    addressSubmitHandle = (event)=>{
        event.preventDefault();
        if(event.target.address.value !== '')
        {
            axios.post('https://medicateindia.herokuapp.com/api/userData/addAddress',{
                address : event.target.address.value
            },{
                headers: {
                'authorization': "JWT "+this.state.token
            }})
            .then(res=>{
                this.setModalShow(false)
                this.fetchUserData()
            })
        }
        
    }
    fetchUserData = ()=>{
        axios.get('https://medicateindia.herokuapp.com/api/userData',{
            headers: {
                'Authorization': "JWT "+this.state.token
            }
        })
        .then(res=>{
            if(res.data === 'expired')
            {
                alert('Session Expired');
                localStorage.removeItem("token")
                this.setState({redirectToLogin:true});
            }
            else{
                this.setState({user : res.data,loading:false})
            }
        })
        .catch(err=>{
            alert(err)
            localStorage.removeItem('token')
            this.setState({redirectToLogin:true})
        })
    }
    componentDidMount()
    {
        if(!this.state.token)
        {
            this.setState({redirectToLogin : true})
        }
        else{
            this.fetchUserData();
        }
    }
    logOutHandle = ()=>{
        localStorage.clear();
        this.props.history.push('/');
    }
    render() {
        if(this.state.redirectToLogin)
        {
            return <Redirect to="/login" />
        }
        if(this.state.loading)
        {
                    return <div className="d-flex justify-content-center mt-2">
                    <Spinner animation="border" role="status" variant="primary">
                      <span className="sr-only">Loading...</span>
                    </Spinner>
                    </div>
       }
        return (
            <React.Fragment>
            <div className="container mt-2">
                <h2>Your Account</h2>
                <div className="row" >
                     <div className="col-sm-3 col-lg-3 col-mg-3 col-9" >
                        <div className="card" style={{width: "18rem",height:"8rem"}}>
                          <div className="card-body">
                            <h3 className="card-title">{this.state.user.Fname+" "+this.state.user.Lname}</h3>
                            <h6 className="card-subtitle mb-2 text-muted"><i>{this.state.user.email}</i></h6>
                            <h6 className="card-subtitle mb-2 text-muted">+91-{this.state.user.mobile}</h6>
                          </div>
                        </div>
                    </div>
                    <div className="col-sm-7 col-lg-7 col-md-1 col-10 ml-sm-3 ml-md-3 ml-lg-3 mt-sm-0 mt-md-0 mt-lg-0 mt-2">
                        <div className="card" style={{width: "100%",height:"8rem"}}>
                          <div className="card-body">
                              <Link to="/customer/orderHistory" className="text-decoration-none text-dark"><figure className="text-center"><img alt="" src="../medicine_orders.svg"/><figcaption style={{fontSize:"12px"}}>Your Orders</figcaption></figure></Link>
                          </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-3">
                 <div className="col-10" >
                        <div className="card" style={{width: "100%"}}>
                          <div className="card-body">
                            <h6 className="card-title text-muted"  style={{letterSpacing:"2px"}}><b>USER INFORMATION</b></h6>
                            <div className="form-row">
                                <div className="col-5 form-group">
                                    <b style={{fontSize : "15px"}} className="text-info">EMAIL</b>
                                    <h6>{this.state.user.email}</h6>
                                    <hr/>
                                </div>
                                <div className="col-5 offset-1 form-group">
                                    <b style={{fontSize : "15px"}} className="text-info">FULL NAME</b>
                                    <h6>{this.state.user.Fname+" "+this.state.user.Lname}</h6>
                                    <hr/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col-5 form-group">
                                    <b style={{fontSize : "15px"}} className="text-info">MOBILE</b>
                                    <h6>+91-{this.state.user.mobile}</h6>
                                    <hr/>
                                </div>
                            </div>
                          </div>
                        </div>
                    </div>
                </div>
                <div className="row my-3">
                 <div className="col-10" >
                        <div className="card" style={{width: "100%"}}>
                          <div className="card-body">
                            <h6 className="card-title text-muted"  style={{letterSpacing:"2px"}}><b>DELIVERY ADDRESS</b></h6>
                            <ol>
                            {
                                this.state.user.address.map((address,index)=>{
                                    return <li key={index}>{address}</li>
                                })
                            }
                            </ol>
                            <button className="btn btn-success btn-lg" onClick={()=>this.setModalShow(true)}>+ Add New Address</button>
                          </div>
                        </div>
                    </div>
                </div>
                <button className="btn btn-danger btn-lg mb-2" onClick={this.logOutHandle}>LogOut</button>





                <Modal show={this.state.modalShow}  onHide={() => this.setModalShow(false)} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
                  <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                      Add Address
                    </Modal.Title>
                  </Modal.Header>
                  <form className="form-group" onSubmit={this.addressSubmitHandle}>
                      <Modal.Body>
                          <div className="input-group">
                              <textarea required id="address" className="form-control"/>
                          </div>
                      </Modal.Body>
                      <Modal.Footer>
                        <input type="submit" className="btn btn-success" value="Submit"/>
                      </Modal.Footer>
                    </form>
                    </Modal>
                    </div>
            </React.Fragment>
        )
    }
}

export default User
